@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
.aside-wrapper {
  position: fixed;
  z-index: 100;
  left: 0;
  top: 75px;
  height: 100%;
  width: 250px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  background-color: #20121f;
}
.aside-wrapper button {
  margin: 5px;
}

.left-nav .item {
  width: 92%;
  padding: 13px 0px 13px 16px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  border-bottom: 1px solid #2d2e2f;
}
.left-nav .section-header {
  display: block;
  margin: 0px 0px 5px 0px;
  width: 100%;
  color: brown;
  font-weight: 600;
  font-size: 0.96rem;
}
.left-nav .link {
  display: block;
  width: 100%;
  text-decoration: none;
}
.left-nav .link-small {
  display: block;
  width: 100%;
  color: #818282;
  font-weight: 400;
  font-size: 0.9rem;
}
.left-nav .link-small:hover {
  color: #fff;
}
.left-nav .link-small:focus {
  color: #fff;
}
.left-nav .link-big {
  display: block;
  width: 93%;
  /* color: #e9e9e9; */
  /* color: #dad5d5; */
  color: rgb(230, 219, 219);
  font-weight: 600;
  font-size: 1.15rem;
}

.left-nav .link-big:hover {
  background: rgba(255, 255, 255, 0.08);
}

.aside-author {
  padding-top: 0.5rem;
  width: 250px;
  background-color: rgba(255, 255, 255, 0.08);
  position: fixed;
  bottom: 0;
  font-size: 0.8rem;
  /* color: #489550; */
  color: rgba(255, 255, 255, 0.7);
}
.aside-name {
  padding-left: 0.5rem;
}
.aside-social {
  margin-top: 0.3rem;
  padding-left: 0.5rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: center;
}
.aside-social img {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 0.5rem;
}

.header-wrapper {
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
}

header .logo {
  margin-left: 3rem;
  width: 3rem;
  height: 3rem;
  cursor: pointer;
}

header .logo-name {
  margin-left: 0.8rem;
  color: white;
  font-size: 1.2rem;
  font-weight: 400;
  cursor: pointer;
  text-decoration: none;
}

header .logo-name-red {
  font-weight: 700;
  color: brown;
}

.main-wrapper {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
  margin-top: 0px;
  margin-left: 250px;
  width: 100%;
  max-width: 100%;
  background-color: #fff;
}

.landing {
  background: linear-gradient(-45deg, #20121f, #ee7752, #20121f);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  height: 100vh;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
}
.landing-nav {
  height: 5rem;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}
.landing-nav-left {
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: center;
  margin-left: 3rem;
}
.landing-logo-nav {
  width: 3rem;
  height: 3rem;
  cursor: pointer;
}
.landing-logo-name {
  margin-left: 0.8rem;
  color: white;
  font-size: 1.2rem;
  font-weight: 400;
  cursor: pointer;
  text-decoration: none;
}
.landing-logo-name-red {
  font-weight: 700;
  color: brown;
}
.landing-nav-right {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  margin-right: 3rem;
  width: 8rem;
}
.landing-nav-right img {
  width: 2.2rem;
  height: 2.2rem;
}
.landing-main {
  height: 90vh;
  width: 100%;
  margin-top: 10rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
}
.landing-logo-main {
  display: block;
  width: 10rem;
  height: 10rem;
}
.landing-desc {
  margin-top: 2rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  text-align: center;
  max-width: 40rem;
  font-size: 1.5rem;
  color: azure;
}
.landing-buttons {
  min-width: 20rem;
  margin-top: 2rem;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
}

.btn-get-started {
  padding: 0.8rem;
  cursor: pointer;
  font-size: 0.9rem;
  letter-spacing: 1px;
  text-transform: uppercase;
  box-shadow: 0 5px 10px #000;
  color: white;
  border-radius: 0.5rem;
  background-color: #3b2a3a;
  outline: none;
}
.btn-source-code {
  padding: 0.8rem;
  cursor: pointer;
  font-size: 0.9rem;
  letter-spacing: 1px;
  text-transform: uppercase;
  border-radius: 0.5rem;
  box-shadow: 0 5px 10px #000;
  outline: none;
}
.author-desc {
  margin-top: 2rem;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@media screen and (max-width: 480px) {
  .landing-nav-left {
    margin-left: 1rem;
  }
  .landing-nav-right {
    margin-right: 1rem;
    width: 6rem;
  }
  .landing-nav-right img {
    width: 1.8rem;
    height: 1.8rem;
  }
  .landing-logo-main {
    width: 7rem;
    height: 7rem;
  }
  .landing-desc {
    padding: 0.5rem;
    margin-top: 1.5rem;
    max-width: 35rem;
    font-size: 1.2rem;
    color: azure;
  }
  .landing-buttons {
    min-width: 15rem;
  }
  .btn-get-started,
  .btn-source-code {
    padding: 0.6rem;
    font-size: 0.7rem;
  }
}

.flex-row {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: center;
}
.flex-column {
