/*--- Product Card ---*/
.prod-card {
  background: #fff;
  position: relative;
  overflow: hidden;
  width: 210px;
}
.prod-card-top {
  position: relative;
}
.prod-card-details {
  padding: 0 10px;
  height: 100%;
  margin-top: 12px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  overflow: hidden;
}
.btn-wishlist-wrapper {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 4px;
  right: 4px;
  background-color: #bcbec2;
  padding: 4px;
  border-radius: 1000px;
}
.prod-badge-wrapper {
  position: absolute;
  top: 15px;
  left: 0px;
}
.prod-card-rating-wrapper {
  position: absolute;
  bottom: -10px;
  right: 0px;
}
.prod-card .card-img {
  width: 210px;
  height: 280px;
}
.prod-card-details .card-title {
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  color: #282c3f;
  margin-bottom: 6px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.prod-card-details .card-description {
  color: #535766;
  font-size: 14px;
  line-height: 1;
  margin-bottom: 0;
  margin-top: 0;
  font-weight: 400;
  width: calc(100% - 10px);
  display: inline-block;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.prod-card-details .card-price {
  margin: 10px 0px 6px 0px;
  line-height: 15px;
}
.card-price-curr {
  font-size: 14px;
  color: #282c3f;
  font-weight: 500;
}
.card-price-orig {
  text-decoration: line-through;
  color: #7e818c;
  font-weight: 400;
  margin-left: 5px;
  font-size: 12px;
}
.card-discountPercentage {
  color: #ff905a;
  font-weight: 400;
  font-size: 12px;
  margin-left: 5px;
}

/*
    CSS-Grid for Product Cards - GEM of an article...
    https://css-tricks.com/responsive-grid-magazine-layout-in-just-20-lines-of-css/
*/

.grid-products {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
  grid-gap: 20px;
  grid-auto-flow: dense;
}

.grid-products .prod-grid-card-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
  grid-gap: 20px;
  grid-auto-flow: dense;
}

.grid-2-cols {
  width: 100%;
  font-size: 0.8rem;
}
.grid-2-cols-row {
  width: 100%;
  display: flex;
  align-items: flex-start;
}
.grid-2-cols-row:not(:first-child) {
  margin-top: 0.4rem;
}
.grid-2-cols-title {
  width: 20%;
  color: #878787;
}
.grid-2-cols-text {
  width: 80%;
}

.toast {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  height: 4rem;
  width: 25rem;
  color: #fff;
  font-size: 0.9rem;
  margin: 1rem 0rem;
  border-radius: 0.2rem;
  box-shadow: 0 0 10px #999;
}
.toast-title {
  font-weight: 600;
}

.toast-top-left {
  transition: transform 0.6s ease-in;
  animation: toast-in-left 0.7s;
}
.toast-top-right {
  transition: transform 0.6s ease-in-out;
  animation: toast-in-right 0.7s;
}
.toast-bottom-right {
  transition: transform 0.6s ease-in-out;
  animation: toast-in-right 0.7s;
}
.toast-bottom-left {
  transition: transform 0.6s ease-in;
  animation: toast-in-left 0.7s;
}

.toast-icon {
  width: 10%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.toast-icon img {
  width: 1.5rem;
  height: 1.5rem;
}
.toast-details {
  width: 80%;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: flex-start;
}

.toast-close {
  margin-top: 1rem;
  width: 10%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.toast-close button {
  border: 0px solid transparent;
  background-color: transparent;
  color: #fff;
  cursor: pointer;
}

@keyframes toast-in-right {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}

@keyframes toast-in-left {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

.toast-list {
  position: absolute;
  z-index: 100;
  right: 1rem;
  top: 1rem;
  width: 50rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
}

.input-text {
  padding: 0.5rem 1rem;
  width: 100%;
  margin-bottom: 0.1rem;
}

.input-search {
  background-color: white;
  background-image: url(/Cardboard-UI/static/media/search-black.f4215dbc.svg);
  background-position: 0.5rem 0.5rem;
  background-repeat: no-repeat;
  padding-left: 2.2rem;
}

.img-responsive {
  display: flex;
  justify-content: center;
  align-items: center;
}

.img-responsive img {
  max-width: 100%;
  max-height: 100%;
}

.img-circular {
  border-radius: 50%;
}

.list-bullets {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 0.8rem;
}
.list-bullets .li-simple,
.list-bullets .li-offer {
  padding-left: 1em;
  text-indent: -0.7em;
}
.list-bullets .li-simple::before {
  content: "•";
  color: rgb(194, 194, 194);
  padding-right: 8px;
}
.list-bullets .li-offer:before {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: 0.8rem 0.8rem;
  display: inline-block;
  width: 0.8rem;
  height: 0.8rem;
  content: "";
  padding-right: 8px;
}

.list-group {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 0.8rem;
}
.list-group-item {
  display: flex;
  align-items: center;
  padding: 0.2rem 0.6rem;
  text-align: left;
  border: 1px solid rgb(194, 194, 194);
}
.list-checkbox {
  width: 1em;
  height: 1em;
  margin-right: 1rem;
}

.navbar {
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  height: 3.2rem;
  margin-bottom: 1rem;
}
.navbar-left {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  margin-left: 4rem;
}
.navbar-right {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
  margin-right: 4rem;
}
.navbar-buttons {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}
.navbar-logo-wrapper {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
}
.navbar-logo-img {
  width: 2rem;
  height: 2rem;
}
.navbar-logo-img img {
  width: 100%;
  height: 100%;
}
.navbar-link {
  text-decoration: none;
  color: white;
  margin-left: 1rem;
}
.navbar-search {
  margin-left: 1rem;
}

.modal {
  position: relative;
  width: 500px;
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-start;
  justify-content: flex-start;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.3rem;
  outline: 0;
}
.modal-title {
  width: 94%;
  padding: 1rem;
  border-bottom: 1px solid #dee2e6;
  font-weight: 600;
  font-size: 1.1rem;
}
.modal-description {
  width: 94%;
  padding: 1rem;
  border-bottom: 1px solid #dee2e6;
}
.modal-buttons {
  width: 94%;
  padding: 1rem;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  align-items: center;
}



body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

/*--- APP START ---*/
.App {
  height: 100vh;
  display: flex;
  flex-flow: column nowrap;
  position: relative;
}
.top-segment {
  position: fixed;
  top: 0;
  height: 5rem;
  width: 100%;
  z-index: 10;
}
.main-segment {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  margin-top: 5rem;
}
/*--- APP END---*/

.hide {
  display: none;
}

td.hljs-ln-code {
  white-space: pre-wrap;
}

